 ========================================================================== */
[class*="uk-stepper-"] {
  font-size: 14px;
  line-height: 20px;
  color: #637582;
  font-family: Avenir, "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-weight: 800;
  font-size: 12px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  width: 100%;
}
[class*="uk-stepper-"]:before {
  content: "";
  display: table-cell;
}
[class*="uk-stepper-"]:after {
  content: "";
  display: table;
  clear: both;
}
[class*="uk-stepper-"] > .uk-step {
  float: left;
  padding: 0 0 8px;
  position: relative;
  z-index: 10;
}
[class*="uk-stepper-"] > .uk-step a {
  font-size: 14px;
  line-height: 20px;
  color: #637582;
  font-family: Avenir, "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-weight: 800;
  display: block;
  font-size: 12px;
  margin: 0 0 -8px;
  padding: 0 0 8px;
}
[class*="uk-stepper-"] > .uk-step + .uk-step {
  margin-left: 12px;
}
[class*="uk-stepper-"] > .uk-step:after {
  background: #a4aeb6;
  bottom: 0;
  content: "";
  display: block;
  height: 8px;
  position: absolute;
  width: 100%;
  z-index: -1;
}
[class*="uk-stepper-"] > .uk-step.uk-active:after {
  background: #00a3cb;
}
[class*="uk-stepper-"] > .uk-step.uk-active ~ .uk-step {
  opacity: 0.6;
}
[class*="uk-stepper-"] > .uk-step.uk-active ~ .uk-step:after {
  opacity: 0.6;
}
.uk-stepper-2 .uk-step {
  width: calc(50% -  6px);
}
.uk-stepper-3 .uk-step {
  width: calc(33.33% -  8px);
}
.uk-stepper-4 .uk-step {
  width: calc(25% -  9px);
}
.uk-stepper-5 .uk-step {
  width: calc(20% -  9.6px);
}
.uk-stepper-6 .uk-step {
  width: calc(16.666% -  10px);
}
.uk-stepper-7 .uk-step {
  width: calc(14.285% -  10.28571429px);
}
.uk-stepper-8 .uk-step {
  width: calc(12.5% -  10.5px);
}
@media (max-width: 767px) {
  [class*="uk-stepper-"] {
    font-size: 10px;
  }
  [class*="uk-stepper-"] > .uk-step a {
    font-size: 10px;
  }
  [class*="uk-stepper-"] > .uk-step:after {
    height: 6px;
  }
}
@media (max-width: 479px) {
  [class*="uk-stepper-"] {
    counter-reset: stepper;
  }
  [class*="uk-stepper-"] > .uk-step {
    padding: 0 0 6px;
    text-indent: -999em;
  }
  [class*="uk-stepper-"] > .uk-step a {
    margin: 0 0 -6px;
    padding: 0 0 6px;
  }
  [class*="uk-stepper-"] > .uk-step + .uk-step {
    margin-left: 8px;
  }
  [class*="uk-stepper-"] > .uk-step:before {
    content: counter(stepper);
    counter-increment: stepper;
    position: absolute;
    left: 0;
    text-align: center;
    text-indent: 0;
    width: 100%;
    z-index: -1;
  }
  [class*="uk-stepper-"] > .uk-step.uk-active {
    text-indent: 0;
  }
  [class*="uk-stepper-"] > .uk-step.uk-active:before {
    content: "";
  }
  .uk-stepper-2 .uk-step {
    width: calc(25% -  4px);
  }
  .uk-stepper-2 .uk-step.uk-active {
    width: calc(75% -  4px);
  }
  .uk-stepper-3 .uk-step {
    width: calc(16.665% -  5.33333333px);
  }
  .uk-stepper-3 .uk-step.uk-active {
    width: calc(66.666% -  5.33333333px);
  }
  .uk-stepper-4 .uk-step {
    width: calc(16.666% -  6px);
  }
  .uk-stepper-4 .uk-step.uk-active {
    width: calc(50% -  6px);
  }
  .uk-stepper-5 .uk-step {
    width: calc(12.5% -  6.4px);
  }
  .uk-stepper-5 .uk-step.uk-active {
    width: calc(50% -  6.4px);
  }
  .uk-stepper-6 .uk-step {
    width: calc(10% -  6.66666667px);
  }
  .uk-stepper-6 .uk-step.uk-active {
    width: calc(50% -  6.66666667px);
  }
  .uk-stepper-7 .uk-step {
    width: calc(10% -  6.85714286px);
  }
  .uk-stepper-7 .uk-step.uk-active {
    width: calc(40% -  6.85714286px);
  }
  .uk-stepper-8 .uk-step {
    width: calc(10% -  7px);
  }
