/* ========================================================================
   Media Queries
 ========================================================================== */
@media screen and (max-width: 600px), screen and (max-device-width: 600px) {
  .events .uk-table th,
  .events .uk-table td img {
    display: none;
  }
  .events .uk-table td {
    float: left;
    height: auto;
    width: auto;
    padding: 20px;
    border-bottom: 1px solid #f1f1f1;
  }
  .events .uk-table td:nth-of-type(1),
  .events .uk-table td:nth-of-type(2),
  .events .uk-table td:nth-of-type(3) {
    background: #f1f1f1;
  }
  .events .uk-table td:nth-of-type(1) {
    border-top: 30px solid #fff;
  }
  .events .uk-table td:nth-of-type(2) {
    display: inline-block;
  }
  .events .uk-table td:nth-of-type(2),
  .events .uk-table td:nth-of-type(3) {
    padding: 0;
    width: 100%;
  }
  .events .uk-button {
    background: #fce300;
    width: 100%;
    margin-bottom: 20px;
  }
  .events .uk-table {
    margin-top: -50px;
  }
  .events .uk-display-inline {
    display: block !important;
  }
  .wistia_embed {
    display: none;
  }
  #dyn-wizard,
  #dyn-wizard-questions,
  #dyn-wizard .wizard-question {
    height: auto;
    overflow: visible;
  }
  h1,
  .uk-h1 {
    font-size: 40px;
    line-height: 40px;
  }
  h2,
  .uk-h2 {
    font-size: 25px;
    line-height: 30px;
  }
  h3,
  .uk-h3 {
    font-size: 18px;
    line-height: 25px;
  }
  h4,
  .uk-h4 {
    font-size: 15px;
    line-height: 22px;
  }
  .uk-sticky-placeholder .uk-button {
    display: block;
  }
  .uk-sticky-placeholder .uk-active .uk-float-right,
  .uk-active #domain-search {
    display: none;
  }
  .uk-sticky-placeholder .uk-float-right {
    text-align: center;
    display: block;
  }
  .uk-sticky-placeholder {
    float: right;
  }
  .uk-button,
  .cta {
    line-height: 20px!important;
    padding: 10px 16px !important;
  }
  [data-uk-sticky] .uk-float-right {
    float: none;
  }
  [data-uk-sticky] .uk-margin-left {
    margin-left: 0 !important;
  }
  [data-uk-sticky] .uk-button {
    margin: 0px 0px 20px;
  }
  .product #wrap-1 .price {
    line-height: 80px;
  }
  .product #wrap-1 .uk-button {
    margin: 0px 0px 20px;
    display: block;
  }
  .product #wrap-1 .uk-active[data-uk-sticky]:before,
  #wrap-1 .uk-active[data-uk-sticky]:after {
    height: 50px;
  }
  .product #wrap-1 .uk-float-right {
    float: none;
  }
  #domain-search input.uk-form-large[type] {
    margin-bottom: 20px;
    width: 90%;
    margin-right: 0;
  }
  .contacting {
    width: auto;
    margin: 15px 0 0;
  }
  .post-pic {
    float: none;
    margin: 0;
  }
}
