  top: -15px;
  left: 30px;
}
.uk-navbar-flip .uk-navbar-nav > li > a {
  padding: 0px 15px;
}
#menu-main-menu .uk-dropdown ul > li > a {
  padding: 10px 10px 10px 0;
  font-size: 16px;
  line-height: 22px;
}
#menu-main-menu .uk-dropdown ul li {
  padding: 10px 20px;
  margin: 0;
  border-top: 1px solid #ECECD9;
}
#menu-main-menu .uk-dropdown .uk-nav-navbar > li:hover {
  background: #f6f6f6;
}
#menu-main-menu .uk-dropdown .uk-nav-navbar > li > a:hover,
#menu-main-menu .uk-dropdown .uk-nav-navbar > li > a:focus {
  background: transparent;
  color: inherit;
}
#navbar-side .uk-navbar-nav > li.uk-open > a,
#navbar-side .uk-navbar-nav > li:hover > a,
#navbar-side .uk-navbar-nav > li > a:focus {
  background-color: transparent;
  color: #bdbdbd;
  border-color: transparent;
}
#navbar-side .uk-navbar-nav > li > a.uk-button-nav {
  border: 2px solid #fce300;
  padding: 0 25px;
}
.uk-navbar-toggle {
  font-size: 22px;
  color: #444;
  text-decoration: none;
  height: 60px;
}
.uk-navbar-toggle:after {
  font-family: Dyn !important;
  content: "\e979";
}
